.wholeCard {
  max-width: 1240px;
}
.imageStyling {
  padding-top: 80px;
  padding-right: 35px;
  height: 300px;
  width: 290px;
}
.card-title {
  text-decoration: underline;
}
.cardCenter {
  text-align: center;
}
.mb-3 {
  border: solid white 5px;
}
h5 {
  text-shadow: 4px 4px 4px #ffe66e;
  font-size: x-large;
}
.marginRight {
  margin-right: 3px;
}

.marginLeft {
  margin-left: 3px;
}

.g-0 {
  background-color: black;
}

.btn-dark:hover {
  cursor: pointer;
  box-shadow: 0 0.4rem 1.4rem 0 #ffe66e;
  transition: transform 150ms;
  transform: scale(1.03);
  color: #ffe66e;
}
